  display: flex;
  font-size: .875rem;
  height: 32px;
  justify-content: center;
  width: 32px
}

.avatar .avatar-content i,
.avatar .avatar-content svg {
  color: #fff;
  font-size: 1rem;
  height: 1rem
}

.avatar .avatar-content i:before {
  vertical-align: top
}

.avatar img {
  border-radius: 50%;
  height: 32px;
  width: 32px
}

.avatar .avatar-status {
  border: 1px solid #fff;
  border-radius: 50%;
  bottom: 0;
  height: .7rem;
  position: absolute;
  right: 0;
  width: .7rem
}

.avatar.avatar-md .avatar-content,
.avatar.avatar-md img {
  font-size: .8rem;
  height: 32px;
  width: 32px
}

.avatar.avatar-md2 .avatar-content,
