  margin-left: 0
}

.sidebar-wrapper {
  background-color: #fff;
  bottom: 0;
  height: 100vh;
  overflow-y: auto;
  position: fixed;
  top: 0;
  transition: left .5s ease-out;
  width: 300px;
  z-index: 10
}

.sidebar-wrapper .sidebar-header {
  font-size: 2rem;
  font-weight: 700;
  padding: 2rem 2rem 1rem
}

.sidebar-wrapper .sidebar-header img {
  height: 1.2rem
}

.sidebar-wrapper .sidebar-toggler.x {
  display: none;
  position: absolute;
  right: 1.75rem;
  top: .25rem
}

.sidebar-wrapper .menu {
  font-weight: 600;
  margin-top: 2rem;
  padding: 0 2rem
}

.sidebar-wrapper .menu .sidebar-title {
  color: #25396f;
  font-size: 1rem;
  font-weight: 600;
  list-style: none;
  margin: 1.5rem 0 1rem;
  padding: 0 1rem
}

.sidebar-wrapper .menu .sidebar-link {
  align-items: center;
  border-radius: .5rem;
  color: #25396f;
  display: block;
  display: flex;
